import React,{useState} from 'react';
import {data} from './data'
import './B1.css'
const List = () => {

 const [peoples , setPeoples] = useState(data)
 const birthdays = peoples.length;
 const RemoveBirthDay = (id)=>{
  const newPeoples = peoples.filter((person)=> person.id !== id);
  setPeoples(newPeoples);
 }
  return (
    <>
      <div className="container">
        <h1>{birthdays} Birthday Today</h1>
        <hr></hr>
        <ul className='BirthdayList'>
         {
          peoples.map((people)=>{
           const {id, name ,age , image} = people;

           return(
          <li key={id}>
           <div className="datacontainer">
            <div className="imageContainer div">
              <img src={image} alt="Avatar"/>
            </div>
            <div className="DetailsContainer div">
                <h3>{name}</h3>
                <p>{age}</p>
            </div>
            <button className='btn2' onClick={()=> RemoveBirthDay(id)} >Remove</button>
           </div>
          </li> 
           )
          })
         }
          
        </ul>
        <button onClick={()=> setPeoples([])} className='btn'>Clear All</button>
      </div>
    </>
  );
};

export default List;
